/* ---------------------------------------- */
/* loading */
/* ---------------------------------------- */
.loading {
  position: fixed;
  z-index: 1001;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ffffff; }

.box-loader {
  display: flex;
  justify-content: center;
  align-items: center; }

.box-loader .dot {
  display: block;
  width: 22px;
  height: 22px;
  margin: 0 6px;
  background-color: #eeeeee;
  border-radius: 50%;
  position: relative; }

.box-loader .color {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0;
  /* どのような要素の変更が予測されているかブラウザーに助言 */
  /* https://developer.mozilla.org/ja/docs/Web/CSS/will-change */
  will-change: opacity, transform;
  transition: opacity 0.3s cubic-bezier(0.38, 1.43, 0.96, 0.27), transform 0.3s cubic-bezier(0.38, 1.43, 0.96, 0.27); }

.box-loader .dot:nth-child(1) .color {
  background-color: #FFEA82; }

.box-loader .dot:nth-child(2) .color {
  background-color: #CD9F78; }

.box-loader .dot:nth-child(3) .color {
  background-color: #8CCCC8; }

.box-loader .dot:nth-child(4) .color {
  background-color: #7D90D7; }

.box-loader .dot:nth-child(5) .color {
  background-color: #FFA4AF; }

.box-loader .dot.is-colored .color {
  opacity: 1;
  transform: scale(1.2, 1.2); }

