/* ---------------------------------------- */
/* hero catch 縦書き */
/* ---------------------------------------- */
.vertical {
  -ms-writing-mode: tb-rl;
  /* for MS IE11 */
  writing-mode: vertical-rl;
  font-weight: 700;
  z-index: 100;
  white-space: nowrap;
  text-align: center;
  left: 50%;
  transform: translate(-50%, 0%); }

.hero-catch p {
  position: relative;
  text-shadow: 0 0 3px pink; }

.hero-catch span {
  position: absolute;
  opacity: 0;
  transform: translateY(10px);
  will-change: transform, opacity; }

.hero-catch .catch-01 {
  font-size: 2rem;
  bottom: 1rem;
  right: 4rem; }

.hero-catch .catch-02 {
  font-size: 1.6rem;
  bottom: 1rem;
  right: 7rem; }

.hero-catch .catch-03 {
  font-size: 1.6rem;
  bottom: 1rem;
  right: 9rem; }

.catch-01,
.catch-02,
.catch-03 {
  animation: fadeIn 0.7s cubic-bezier(0.455, 0.03, 0.515, 0.955) forwards; }

.catch-01 {
  animation-delay: 2.8s; }

.catch-02 {
  animation-delay: 3.2s; }

.catch-03 {
  animation-delay: 3.4s; }

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(5px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

