/* ---------------------------------------- */
/* footer */
/* ---------------------------------------- */
@media (max-width: 800px) {
  footer {
    padding-right: 0;
    padding-left: 0; }
  footer .footer-logo {
    display: inline-block; }
  footer .footer-nav {
    margin-bottom: 18px;
    padding-left: 40px;
    padding-right: 0; }
  footer .footer-nav-unit li {
    margin-bottom: 16px;
    margin-right: 24px;
    padding: 0; }
  footer .footer-nav-unit li::after {
    display: none; }
  footer .inner {
    display: block;
    padding-top: 26px;
    padding-bottom: 26px;
    padding-left: 0;
    padding-right: 0; }
  footer .footer-logo {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 26px;
    border-bottom: 1px solid #DDDDDD;
    text-align: center;
    display: flex;
    justify-content: center; }
  footer .footer-logo img {
    width: 52px;
    height: 40.3px; }
  footer .footer-legal {
    display: block;
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px; }
  footer .copyright {
    margin-left: 0;
    clear: both;
    text-align: center; } }

/* ---------------------------------------- */
/* header */
/* ---------------------------------------- */
@media (max-width: 1000px) {
  /* ナビゲーションボタン */
  .nav-button {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 29px;
    height: 24px;
    box-sizing: content-box;
    padding: 0;
    outline: none;
    border: none;
    background: none;
    cursor: pointer;
    color: #7D90D7;
    transition: all 0.4s ease-in-out; }
  .nav-button::before,
  .nav-button::after {
    content: '';
    height: 2px;
    background-color: currentColor;
    transform: translateY(10px);
    transition: all .3s ease-in-out;
    display: block; }
  .nav-button::before {
    transform: translateY(-10px);
    box-shadow: 0 11px currentColor; }
  /* ナビゲーションボタン閉じるボタン */
  .open .nav-button {
    z-index: 1000; }
  .open .nav-button::before {
    transform: rotate(-45deg);
    height: 1px;
    box-shadow: none; }
  .open .nav-button::after {
    transform: rotate(45deg);
    height: 1px; }
  header nav {
    display: block;
    width: 100%;
    height: 0;
    top: -100%;
    right: 100%;
    margin-top: -100px;
    background-color: transparent;
    will-change: top, background-color;
    transition: top 0.4s ease-in-out, background-color 0.4s ease-in-out; }
  header ul a.js-anchor {
    color: transparent; }
  .open header nav {
    width: 100%;
    height: 100%;
    margin-top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.8); }
  .open header ul {
    display: block;
    background-color: transparent; }
  .open header ul li {
    margin-top: 0.5rem; }
  .open header ul a.js-anchor {
    color: #ffffff;
    padding-top: 0; } }

@media (orientation: landscape) and (max-width: 1000px) {
  .open header ul {
    display: flex;
    flex-wrap: wrap; } }
